body {
  margin: 0;
  padding: 0;
  font-family: "TT Lakes"; }

a {
  text-decoration: none; }

.rotate {
  transform: rotate(180deg); }

.text-active {
  display: inline; }

.inactive {
  opacity: 0.2; }

.body-overflow-hidden {
  overflow-y: hidden; }

@-webkit-keyframes rotate {
  to {
    transform: rotate(360deg); } }

@keyframes rotate {
  to {
    transform: rotate(360deg); } }

@-webkit-keyframes scale {
  to {
    transform: scale(0.5); } }

@keyframes scale {
  to {
    transform: scale(0.5); } }

@-webkit-keyframes call {
  25% {
    transform: scale(1.2); }
  50% {
    transform: rotate(-20deg); }
  75% {
    transform: rotate(20deg); }
  100% {
    transform: scale(1); } }

@keyframes call {
  25% {
    transform: scale(1.2); }
  50% {
    transform: rotate(-20deg); }
  75% {
    transform: rotate(20deg); }
  100% {
    transform: scale(1); } }

@-webkit-keyframes message {
  25% {
    transform: scale(1.2); }
  75% {
    transform: rotate(180deg); }
  100% {
    transform: scale(1); } }

@keyframes message {
  25% {
    transform: scale(1.2); }
  75% {
    transform: rotate(180deg); }
  100% {
    transform: scale(1); } }
